const initialState = {
  userData: {},
  isFetching: false,
  isError: false,
  error: {},
  tokens: {}
};

const asyncReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'FETCH_USER':
      return Object.assign({}, state, {
        isFetching: true,
        isError: false
      });
    case 'FETCHED_USER':
      return Object.assign({}, state, {
        userData: action.data,
        isFetching: false,
        isError: false
      });
    case 'LOGGED_IN':
      window.open(`${action.reroute}`, '_self');
      return Object.assign({}, state, {
        isFetching: false,
        isError: false
      });

    case 'SESSION_EXPIRED':
      return Object.assign({}, state, {
        userData: {},
        isFetching: false,
        isError: false,
        tokens: {}
      });
    case 'SESSION_VALID':
      return Object.assign({}, state, {
        isFetching: false,
        isError: false,
        tokens: action.tokens
      });

    case 'RECEIVE_ERROR':
      return Object.assign({}, state, {
        isError: true,
        isFetching: false,
        error: action.error
      });
    default:
      return state;
  }
};

export default asyncReducer;
