import { IonText, IonLabel } from '@ionic/react';
import React from 'react';
import './TextWithLabel.css';

interface textWithLabelProps {
  textValue: string;
  labelValue: string;
}

const TextWithLabel: React.FunctionComponent<textWithLabelProps> = props => {
  return (
    <>
      <IonLabel className='label'>{props.labelValue}</IonLabel>
      <br />
      <IonText color='primary'>
        <span style={{ fontSize: '1.5em' }}>{props.textValue}</span>
      </IonText>
    </>
  );
};
export default TextWithLabel;
