import store from '../store';
import {
  CognitoUserPool,
  CognitoUser,
  AuthenticationDetails
} from 'amazon-cognito-identity-js';
const data = {
  UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
  ClientId: `${process.env.REACT_APP_CLIENT_ID}`
};
let userPool = new CognitoUserPool(data);
let cognitoUser = userPool.getCurrentUser();

export const fetch_post = () => {
  return {
    type: 'FETCH_USER'
  };
};

export const receive_post = post => {
  return {
    type: 'FETCHED_USER',
    data: post
  };
};

export const receive_error = error => {
  return {
    type: 'RECEIVE_ERROR',
    error: error
  };
};

export const logged_in = reroute => {
  return {
    type: 'LOGGED_IN',
    reroute: reroute
  };
};
export const session_expired = reroute => {
  return {
    type: 'SESSION_EXPIRED',
    reroute: reroute
  };
};
export const session_valid = tokens => {
  return {
    type: 'SESSION_VALID',
    tokens: tokens
  };
};
