export const get_member_attributes = () => {
  store.dispatch(fetch_post());
  return function(dispatch, getState) {
    cognitoUser = userPool.getCurrentUser();
    let attributeMap = {};
    if (cognitoUser != null) {
      cognitoUser.getSession(() => {
        cognitoUser.getUserAttributes((err, result) => {
          if (err) {
            console.log(err);
            store.dispatch(receive_error(err));
            return err;
          }
          result.map((attribute, i) => {
            let attributeName = attribute.getName();
            switch (attributeName) {
              case 'sub':
                attributeMap.sub = attribute.getValue();
                break;
              case 'given_name':
                attributeMap.firstName = attribute.getValue();
                break;
              case 'family_name':
                attributeMap.lastName = attribute.getValue();
                break;
              case 'custom:role':
                attributeMap.role = attribute.getValue();
                break;
              case 'email':
                attributeMap.email = attribute.getValue();
                break;
              default:
                // do nothing;
                break;
            }
            // if (i === result.length - 1) {
            //   //   setMember(
            //   // member : attributeMap;
            //   // return { member: attributeMap };
            //   //   );
            //   // console.log('function ' + JSON.stringify({ ...attributeMap }));
            //   // AuthReducer(attributeMap, { type: SUCCESS });
            //   // return { attributeMap };

            //   store.dispatch(receive_post(attributeMap));
            // }
          });
          store.dispatch(receive_post(attributeMap));
        });
      });

      // return {firstName: 'Shady'};
    }
  };
};

export const submit_login = payload => {
  store.dispatch(fetch_post());
  return function(dispatch, getState) {
    const { username, password, reroute } = payload;
    const user = username.replace(/\s/g, '');
    const authenticationData = {
      Username: user,
      Password: password
    };
    const authenticationDetails = new AuthenticationDetails(authenticationData);

    const userData = {
      Username: user,
      Pool: userPool
    };
    const cognitoUser = new CognitoUser(userData);
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: result => {
        store.dispatch(logged_in('#/account'));

        //   if (reroute != null) {
        //     window.open(`${reroute}`, '_self');
        //   } else

        // return { isLoading: false };
      },

      onFailure: err => {
        //   alert(JSON.stringify(err));
        //   setAuthAlert({ ...authAlert, show: true, message: err });
        // setFormErrors(err);
        store.dispatch(receive_error(err));
        return { error: err };
      }
    });
  };
};
export const is_logged_in = reroute => {
  return function(dispatch, getState) {
    cognitoUser = userPool.getCurrentUser();

    if (cognitoUser != null) {
      cognitoUser.getSession((err, session) => {
        if (err) {
          // alert(err);
          store.dispatch(session_expired(reroute));

          if (reroute != null) {
            window.open(`#/login?${reroute}`, '_self');
          } else window.open('#/login', '_self');

          // return window.open('#/login', '_self');
          return err;
        }
        // console.log('session validity: ' + session.isValid());

        // console.log('session Access: ' + session.getAccessToken().getJwtToken());
        // console.log('session ID: ' + session.getIdToken().getJwtToken());
        // console.log('session Refresh: ' + session.getRefreshToken().getJwtToken()
        // );
        // let idToken = session.getIdToken().getJwtToken();
        // let accessToken = session.getAccessToken().getJwtToken();
        store.dispatch(session_valid());
        //to do pass sourceURL here for login callback
        if (session.isValid() === true && reroute && reroute !== null) {
          window.open(`${reroute}`, '_self');
        }
        if (session.isValid() === false) {
          window.open(`#/login`, '_self');
        }
      });
    }
    // to do pass a sourceURL in the paras to callback after login
    else {
      store.dispatch(session_expired(reroute));
      window.open(`#/login`, '_self');
    }
  };
};
export const sign_out = reroute => {
  return function(dispatch, getState) {
    if (cognitoUser != null) {
      cognitoUser.signOut();
      store.dispatch(session_expired());
      // todo base64 a url paramter that login.tsx will call back after auth
    }
    window.open(`#/login?${reroute}`, '_self');
  };
};
export const get_token = () => {
  return function(dispatch, getState) {
    if (cognitoUser != null) {
      cognitoUser.getSession((err, session) => {
        if (err) {
          return err;
        }
        let idToken = session.getIdToken().getJwtToken();
        let accessToken = session.getAccessToken().getJwtToken();
        store.dispatch(
          session_valid({ idToken: idToken, accessToken: accessToken })
        );
        // return { ...state, idToken: idToken, accessToken: accessToken };
        // console.log(session.getIdToken().getJwtToken());
      });
    }
  };
};
