import {
  IonButton,
  IonContent,
  IonIcon,
  IonRow,
  IonGrid,
  IonItem,
  IonList,
  IonLabel,
  IonInput,
  IonText,
  IonLoading,
  IonToast
} from '@ionic/react';

import React, { useState, useRef, useEffect } from 'react';
import Header from '../../components/Header/Header';
import { lock, mail } from 'ionicons/icons';
import Analytics from '../../components/SEO/Analytics';
import SlyHelmet from '../../components/SEO/Helmet';
import Footer from '../../components/Footer/Footer';
// import { AuthReducer, LOGIN, IS_LOGGED_IN } from '../../reducers/auth';
import { submit_login, is_logged_in } from '../../actions/fetchAction';
import { connect } from 'react-redux';
interface loginProps {
  title: string;
  route: string;
  dispatch?: any;
  data?: {
    isFetching?: boolean;
    isError?: boolean;
    error?: { message?: string };
  };
}
const Login: React.FunctionComponent<loginProps> = props => {
  const [isFormInvalid, setIsFormInvalid] = useState(true);

  const emailRef: any = useRef(null);
  const passwordRef: any = useRef(null);

  // const [state, dispatch] = useReducer(AuthReducer, undefined);
  // const [dispatch] = useReducer(AuthReducer, {});

  const canBeSubmitted = async () => {
    emailRef.current != null &&
    passwordRef.current != null &&
    emailRef.current.value.length > 0 &&
    passwordRef.current.value.length >= 8
      ? setIsFormInvalid(false)
      : setIsFormInvalid(true);
  };
  const submit = async () => {
    try {
      setIsFormInvalid(true);

      if (isFormInvalid == false) {
        let authObject: object = {
          username: emailRef.current.value,
          password: passwordRef.current.value,
          reroute: '#/account'
        };
        // dispatch({
        //   type: LOGIN,
        //   payload: { ...authObject }
        // });
        props.dispatch(submit_login(authObject));
      }
    } catch (e) {
      setIsFormInvalid(false);
    }
  };
  useEffect(() => {
    props.dispatch(is_logged_in('#/account'));
  }, []);

  return (
    <>
      <SlyHelmet {...props} />

      <Analytics pageName='login' />
      <Header pageTitle={'Login'} hideLogin={true} />
      <IonContent>
        {props.data && props.data.isFetching == true && (
          <IonLoading
            isOpen={props.data.isFetching}
            // onDidDismiss={}
            message={'Authorizing...'}
            duration={2000}
          />
        )}
        {props.data && props.data.error && (
          <IonToast
            position='top'
            isOpen={props.data.error.message != null}
            onDidDismiss={() => {
              if (props.data && props.data.error) {
                props.data.error = {};
              }
            }}
            message={props.data.error.message}
            duration={5000}
            buttons={[
              {
                side: 'end',
                icon: 'star',
                text: 'Dismiss',
                handler: () => {
                  if (props.data && props.data.error) {
                    props.data.error = {};
                  }
                }
              }
            ]}
          />
        )}

        <IonGrid>
          <IonRow className='ion-justify-content-center'></IonRow>
          <IonRow className='ion-justify-content-center'>
            <form
              style={{ width: '300px' }}
              onSubmit={e => {
                e.preventDefault();
                submit();
              }}
            >
              <IonRow className='ion-justify-content-center'>
                <IonText color='danger'>
                  {/* <p>
                    {props.data && props.data.error
                      ? props.data.error.message
                      : null}
                  </p> */}
                </IonText>
              </IonRow>
              <IonList>
                <IonItem>
                  <IonLabel
                    onClick={() => emailRef.current.focus()}
                    position='floating'
                  >
                    Email
                  </IonLabel>
                  <IonInput
                    ref={emailRef}
                    type='email'
                    // value={email}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      style={{ paddingLeft: '250px', position: 'absolute' }}
                      color='medium'
                      icon={mail}
                    />
                  </IonInput>
                </IonItem>
                <br />
                <IonItem>
                  <IonLabel
                    onClick={() => passwordRef.current.focus()}
                    position='floating'
                  >
                    Password
                  </IonLabel>
                  <IonInput
                    ref={passwordRef}
                    type='password'
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      style={{ paddingLeft: '250px', position: 'absolute' }}
                      color='medium'
                      icon={lock}
                    />
                  </IonInput>
                </IonItem>
              </IonList>
              {!isFormInvalid && <button hidden type='submit'></button>}
              <IonRow className='ion-justify-content-center'>
                <IonButton
                  disabled={isFormInvalid}
                  style={{ width: '90%' }}
                  onClick={() => {
                    submit();
                  }}
                >
                  <IonIcon slot='start' icon={lock}></IonIcon>
                  {props.data && props.data.isFetching == true
                    ? 'Logging in'
                    : 'Log in'}
                </IonButton>
              </IonRow>
              {/* {props && JSON.stringify(props)} */}
            </form>
          </IonRow>
          <br />
          <IonRow className='ion-justify-content-center'>
            <IonButton fill='outline' size='small' href='#/signup'>
              Sign UP
            </IonButton>
          </IonRow>
        </IonGrid>
      </IonContent>
      <Footer />
    </>
  );
};
const mapStateToProps = (state: any) => {
  return {
    data: state
  };
};
export default connect(mapStateToProps)(Login);
