import {
  IonButton,
  IonButtons,
  IonHeader,
  IonMenuButton,
  IonTitle,
  IonToolbar,
  IonText,
  IonCol,
  IonFab,
  IonFabButton,
  IonIcon,
  IonRouterLink,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonPopover,
  IonFabList
} from '@ionic/react';

import React from 'react';
import './Header.css';
import { person, calendar } from 'ionicons/icons';
import { connect } from 'react-redux';
import { sign_out } from '../../actions/fetchAction';

interface headerProps {
  pageTitle: string;
  hideLogin?: boolean;
  hideSignUp?: boolean;
  loggedIn?: boolean;
  dispatch?: any;
  data?: any;
}
const Header: React.FunctionComponent<headerProps> = props => {
  return (
    <>
      <IonHeader translucent className='navbar'>
        <IonToolbar className='toolbar'>
          <IonButtons slot='start'>
            <IonMenuButton />
          </IonButtons>
          <IonTitle
            className='on-hide-md-down animated fadeIn'
            onClick={() => window.open('/#/home', '_self')}
          >
            Sly Guard
            <IonText color='medium'>
              <p
                style={{ fontSize: '.6em', margin: 0 }}
                className='animated zoomIn'
              >
                Protection Services
              </p>
            </IonText>
          </IonTitle>
          <IonTitle className='ion-hide-md-up animated fadeIn toolbar-title'>
            {props.pageTitle}
          </IonTitle>
          <IonButtons slot='end' className='ion-hide-md-down'>
            <IonButton>Guards</IonButton>
            <IonCol size='1' slot='end'></IonCol>
            <IonButton>Clients</IonButton>
          </IonButtons>
          {props.hideSignUp && !props.data.userData.firstName && (
            <IonCol className='ion-hide-md-down' size='1' slot='end'></IonCol>
          )}
          <IonButtons className='ion-hide-md-down' slot='end'>
            {props.data.userData.firstName && (
              <IonButton href={'#/account'} color='primary'>
                <IonIcon
                  style={{ fontSize: '1em' }}
                  slot='start'
                  icon={person}
                ></IonIcon>
                {props.data.userData.firstName}
              </IonButton>
            )}
          </IonButtons>

          {props.loggedIn && (
            <>
              <IonButtons slot='end'>
                <IonButton
                  className='ion-text-nowrap ion-padding-horizontal'
                  style={{ fontSize: '.6em' }}
                  color='medium'
                  onClick={() => props.dispatch(sign_out())}
                >
                  Logout
                </IonButton>
              </IonButtons>
            </>
          )}

          {!props.hideSignUp && (
            <>
              <IonButtons slot='end' className='ion-hide-md-down'>
                <IonButton
                  color='medium'
                  href={'#/signup'}
                  fill='outline'
                  class='ion-text-uppercase'
                  // style={{ width: '115px' }}
                >
                  Get Started
                </IonButton>
              </IonButtons>
            </>
          )}
        </IonToolbar>

        {!props.hideLogin && (
          <IonFab vertical='top' horizontal='end' slot='fixed'>
            <IonFabButton href={'#/login'}>
              <IonIcon mode='ios' icon={person} />
            </IonFabButton>
          </IonFab>
        )}
      </IonHeader>
    </>
  );
};
const mapStateToProps = (state: any) => {
  return {
    data: state
  };
};
export default connect(mapStateToProps)(Header);
